<template>
  <div id="adduser">
    <b-container :toast="{ root: true }" fluid="sm" position="position-fixed"></b-container>

    <div class="row justify-content-center">
      <div class="col-lg-10">
        <form @submit.prevent="addadmin()">
          <div class="row">

            <label for="ipsupassport" class="form-label">Psu Passport</label>
            <div class="mb-3 col-md-4"><input type="text" :class="{ 'form-control': true, 'is-invalid': !isValid }"
                v-model="psupassport" id="ipsupassport" aria-describedby="helpId" placeholder="PSU Passport">

            </div>
            <div class="mb-3 col-md-5">

              <input type="text" class="form-control-plaintext text-center" :value="fullname" aria-describedby="helpId"
                readonly>
            </div>

            <div class="mb-3 col-md-3">
              <div class="d-grid gap-2">
                <transition name="flip">
                  <button type="submit" class="btn btn-primary" v-if="canSub">เพิ่มผู้ใช้</button>


                </transition>
              </div>
            </div>

          </div>
        </form>


      </div>
    </div>
    <hr />

    <div class="row justify-content-center">
      <div class="col-md-10 col-lg-8">
        <label for="isearch" class="form-label">ค้นหา</label>
        <div class="mb-3 "><input type="text" :class="{ 'form-control': true }" v-model="txtSearch" id="isearch"
            aria-describedby="helpId" placeholder="ค้นหา">

        </div>


      </div>
      <div class="mb-3 col-md-2 col-lg-2">
        <label for="spage" class="form-label">จำนวนหน้า</label>
        <select class="form-control" id="spage" v-model="PerPage">
          <option v-for="pageo in PageOptions" :key="pageo" :value="pageo">{{ pageo }}</option>
        </select>
      </div>
      <b-pagination pills align="center" v-model="CurrentPage" :total-rows="filteradmin.length" :per-page="PerPage">
      </b-pagination>
    </div>
    <div class="d-none d-md-block">
      <div class="row justify-content-center ">

        <div class="col-lg-10">
          <table class="table table-striped table-hover">
            <thead>
              <tr>
                <th>PSU Passport</th>
                <th>ชื่อ</th>
                <th>คณะ</th>
                <th>สาขา / หน่วยงาน</th>
                <th>เปิดการใช้งาน</th>
                <th>การดำเนินการ</th>
              </tr>
            </thead>
            <tbody v-if="filteradmin.length">

              <tr v-for="(admin) in pageadmin" :key="admin.psupassport">

                <td>{{ admin.psupassport }}</td>

                <td>{{ admin.FULL_NAME }}</td>
                <td>{{ admin.FAC_NAME_THAI }}</td>
                <td>{{ admin.DEPT_NAME_THAI }}</td>
                <td>
                  <div class="form-check form-switch">
                    <input class="form-check-input" type="checkbox" :checked="admin.enable == 1"
                      @change="toggleenable($event, admin.psupassport)">
                  </div>
                </td>
                <td>
                  <div class="d-grid gap-2">
                    <transition name="flip">
                      <button type="button" @click="removeadmin(admin.psupassport, index)" class="btn btn-sm btn-danger"
                        v-if="admin.enable == 0">ลบ</button>
                    </transition>

                  </div>
                </td>

              </tr>

            </tbody>
            <tbody v-else>
              <tr>
                <td colspan="6" class="text-center">ไม่พบข้อมูล</td>
              </tr>
            </tbody>


          </table>
          <div class="mt-3 justify-content-center">

            <b-pagination pills align="center" v-model="CurrentPage" :total-rows="filteradmin.length"
              :per-page="PerPage">
            </b-pagination>
          </div>

        </div>
      </div>
    </div>

    <div class="d-block d-md-none">
      <div class="row justify-content-center ">
        <div class="col-lg-10">
          <div
            :class="{ 'card': true, 'mb-3': true, 'border-danger': admin.enable == 0, 'border-success': admin.enable == 1 }"
            v-for="(admin) in pageadmin" :key="admin.psupassport">

            <div :class="{ 'card-body': true, 'text-danger': admin.enable == 0, 'text-success': admin.enable == 1 }">
              <h5 class="card-title">{{ admin.psupassport }}</h5>
              <p class="card-text">
                {{ admin.FULL_NAME }} <br />
                {{ admin.FAC_NAME_THAI }} <br />
                {{ admin.DEPT_NAME_THAI }} <br />
              </p>
            </div>
            <div class="card-footer">

              <div class="row justify-content-center">
                <div class="col-6 ">
                  <div class="form-check form-switch">
                    <input class="form-check-input" type="checkbox" :id="'chk_' + admin.psupassport"
                      :checked="admin.enable == 1" @change="toggleenable($event, admin.psupassport)">
                    <label
                      :class="{ 'form-check-label': true, 'text-danger': admin.enable == 0, 'text-success': admin.enable == 1 }"
                      :for="'chk_' + admin.psupassport">เปิดการใช้งาน</label><br />

                  </div>
                </div>
                <div class="col-6">
                  <div class="d-grid gap-2">
                    <transition name="flip">
                      <button type="button" @click="removeadmin(admin.psupassport, index)" class="btn btn-danger"
                        v-if="admin.enable == 0">ลบ</button>
                    </transition>
                  </div>
                </div>
              </div>


            </div>
          </div>
          <div class=" mt-3 mb-3">

            <b-pagination pills align="center" v-model="CurrentPage" center :total-rows="filteradmin.length"
              :per-page="PerPage">
            </b-pagination>
          </div>


        </div>
      </div>
    </div>


  </div>

</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import { useToast } from 'bootstrap-vue-3'


export default {
  name: "AddUser",
  data() {
    return {
      psupassport: null,
      fullname: "โปรดกรอกให้ถูกต้อง",
      canSub: false,
      toast: useToast(),
      isValid: true,
      admins: [],
      txtSearch: "",
      CurrentPage: 1,
      PerPage: 5,
      PageOptions: [5, 10, 25, 50, 100]
    }
  },
  methods: {
    async addadmin() {
      const dat = {
        psupassport: this.psupassport
      }
      let res = await axios.post("user/admin",
        dat,
        {
          headers: {
            Authorization: "Bearer " + this.token
          }
        }
      );
      res = res.data;
      // console.log(res)
      if (res.http == 200) {
        this.toast.show(
          { title: 'สำเร็จ', body: res.data.message },
          { pos: 'top-right', variant: 'success' }
        )
        // this.loadadmin()
        this.admins.push(res.data.detail)
        // this.filteradmin
        this.psupassport = ''
      } else {
        this.toast.show(
          { title: 'ผิดพลาด', body: res.data.message },
          { pos: 'top-right', variant: 'danger' }
        )
        this.isValid = false
      }
    },
    async loadadmin() {
      let res = await axios.get('user/admin', {
        headers: {
          Authorization: "Bearer " + this.token
        }
      })
      res = res.data
      this.admins = res.data.admin
    },
    async removeadmin(username) {
      // alert(username)

      let idx = this.admins.findIndex((el) => {
        return el.psupassport == username
      })

      this.$swal({
        title: 'ยืนยันการลบ ?',
        text: "ท่านต้องการที่จะนำ " + this.admins[idx].FULL_NAME + " อกกจาก Admin ใช่หรือไม่",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: 'ยกเลิก',
        confirmButtonText: 'ใช่'
      }).then(async (result) => {
        if (result.isConfirmed) {
          //ส่งลบ

          try {
            let res = await axios.delete('user/admin/' + username, {
              headers: {
                Authorization: "Bearer " + this.token
              }
            }
            )
            res = res.data
            if (res.http == 200) {
              // this.$swal(
              //   'สำเร็จ',
              //   res.data.message,
              //   'success'
              // )

              this.toast.show(
                { title: 'สำเร็จ', body: res.data.message },
                { pos: 'top-right', variant: 'success' }
              )
              this.admins.splice(idx, 1)
            } else {
              // this.$swal(
              //   'ผิดพลาด',
              //   res.data.message,
              //   'error'
              // )
              this.toast.show(
                { title: 'ผิดพลาด', body: res.data.message },
                { pos: 'top-right', variant: 'danger' }
              )
            }
          } catch (error) {
            // this.$swal(
            //   'ผิดพลาด',
            //   "Server Error",
            //   'error'
            // )
            this.toast.show(
              { title: 'ผิดพลาด', body: "Server Error" },
              { pos: 'top-right', variant: 'danger' }
            )
          }






        }


      }
      );
    },
    async toggleenable(event, username) {
      let idx = this.admins.findIndex((el) => {
        return el.psupassport == username
      })
      // console.log(event.target.checked)
      // console.log(username)
      // console.log(this.admins[idx])
      let val = event.target.checked ? "1" : "0"
      let data = {
        enable: val
      }

      let res = await axios.put('user/admin/' + username, data, {
        headers: {
          Authorization: "Bearer " + this.token
        }
      })

      res = res.data
      // console.log(res)
      // console.log(this.admins[idx].enable)

      if (res.http == 200) {
        this.admins[idx].enable = val
      }
      // console.log(this.admins[idx].enable)


      // console.log(idx)
    }
  }, computed: {
    ...mapGetters(['token']),
    filteradmin() {
      console.log("filteradmin")

      let tmpadmin = this.admins

      //search from text
      tmpadmin = tmpadmin.filter((item) => {
        let rese = false;



        // console.log(item)

        for (const key in item) {
          if (item[key] != null && item[key].includes(this.txtSearch)) {
            rese = true
          }

        }

        return rese
        // return rese
      })




      tmpadmin = tmpadmin.sort((a, b) => {
        let fa = a.psupassport.toLowerCase(), fb = b.psupassport.toLowerCase()
        if (fa < fb) {
          return -1
        }
        if (fa > fb) {
          return 1
        }
        return 0
      })

      // tmpadmin.reverse()
      return tmpadmin
    }, pageadmin() {
      console.log("pageadmin")

      let tmpadmin = this.admins

      //search from text
      tmpadmin = tmpadmin.filter((item) => {
        let rese = false;

        // console.log(item)
        for (const key in item) {
          if (item[key] != null && item[key].includes(this.txtSearch)) {
            rese = true
          }

        }

        return rese
        // return rese
      })




      tmpadmin = tmpadmin.sort((a, b) => {
        let fa = a.psupassport.toLowerCase(), fb = b.psupassport.toLowerCase()
        if (fa < fb) {
          return -1
        }
        if (fa > fb) {
          return 1
        }
        return 0
      })

      return tmpadmin.slice((this.CurrentPage - 1) * this.PerPage, this.CurrentPage * this.PerPage)
    }
  }, watch: {

    async psupassport(value) {
      console.log("psupassport")
      // console.log(Number.isInteger(value))
      const tonumber = parseInt(value)
      if ((Number.isInteger(tonumber) && value.length == 10) || (value.indexOf('.') > 0 && value.indexOf('.') < value.length - 1)) {
        const dat = {
          psupassport: this.psupassport
        }
        let res = await axios.post("user/psucheck",
          dat,
          {
            headers: {
              Authorization: "Bearer " + this.token
            }
          }
        );
        res = res.data;
        if (res.http == 200) {
          this.fullname = res.data.name
          this.canSub = true
          this.isValid = true

        } else {
          this.fullname = "ไม่พบรายชื่อ"
          this.canSub = false
          this.isValid = false

        }
        // console.log(res);
      } else {
        this.fullname = "โปรดกรอกให้ถูกต้อง"
        this.canSub = false
        this.isValid = false
        if (value.length == 0) {
          this.isValid = true

        }

      }
    }/*
    */
  }, created() {
    this.loadadmin()
  }

}
</script>

<style>
table>tbody>tr>td {
  vertical-align: middle;
}

flip-enter-from {
  /* transform: rotateX(-70deg); */
  opacity: 0;
}

flip-enter-to {
  /* transform: rotateX(0); */
  opacity: 1;
}

.flip-enter-active {
  animation: flip-in-ver-right 0.6s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}

.flip-leave-active {
  animation: flip-out-ver-right 0.6s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}

@keyframes flip-in-ver-right {
  0% {
    transform: rotateY(-80deg);
    opacity: 0;
  }

  100% {
    transform: rotateY(0);
    opacity: 1;
  }
}


@keyframes flip-out-ver-right {
  0% {
    transform: rotateY(0);
    opacity: 1;
  }

  100% {
    transform: rotateY(70deg);
    opacity: 0;
  }
}

@keyframes roll-in-blurred-left {
  0% {
    transform: translateX(-1000px) rotate(-720deg);
    filter: blur(50px);
    opacity: 0;
  }

  100% {
    transform: translateX(0) rotate(0deg);
    filter: blur(0);
    opacity: 1;
  }
}

@keyframes roll-out-right {
  0% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }

  100% {
    transform: translateX(1000px) rotate(540deg);
    opacity: 0;
  }
}
</style>