<template>
  <div id="gauge_example">
    <apexchart type="radialBar" height="350" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>

<script>
export default {
  data() {
    return {
      series: [0],
      chartOptions: {
        chart: {
          height: 350,
          type: 'radialBar',
          offsetY: -10
        },
        plotOptions: {
          radialBar: {
            startAngle: -135, //135
            endAngle: 135,
            dataLabels: {
              name: {
                fontSize: '16px',
                color: undefined,
                offsetY: 120
              },
              value: {
                offsetY: -10,
                fontSize: '22px',
                color: undefined,
                formatter: function (val) {
                  return val + "%";
                }
              }
            }
          }
        },

        stroke: {
          dashArray: 1.5
        },
        // fill: { colors: ['#35db37'] },
        labels: ['Median Ratio'],
        colors: ['#35db99'],
      },
    }
  }, methods: {
    randompercent() {
      const val = (Math.random() * 100).toFixed()
      let col = ''

      if (val < 30) {
        col = '#e35656'
      } else if (val < 50) {
        col = '#e3ad56'

      } else if (val < 70) {
        col = '#80e356'

      } else {
        col = '#35db37'

      }

      this.series = [val]
      // this.chartOptions.fill.colors = [col]
      // this.chartOptions.colors[0] = col
      // this.chartOptions.colors = [col]
      this.chartOptions.colors[0] = col
      // console.log(col)
      // console.log(this.chartOptions.colors)
    }
  }, mounted() {
    this.timers = setInterval(() => {
      // console.log('randoms')
      this.randompercent()
    },1500);
  }, beforeUnmount() {
    clearInterval(this.timers)
  }
}
</script>

