export default { // ใช้รับข้อมูลจาก Store หรือประมวล Store (จะเป็นฟังก์ชันใน compute ...mapGetters(['ฟังก์ชัน'])  or getters)
    username(state) {
        return state.username
    },
    token(state) {
        return state.token
    },
    role(state) {
        return parseInt(state.role)
    },
    isadmin(state) {
        return state.role >= 1000
    }
}