<template>

  <div id="LoginView" class="page page-center">
    <b-container :toast="{ root: true }" fluid="sm" position="position-fixed"></b-container>

    <div class="row align-items-center h-100">
      <div class="col-lg-5 mx-auto">
        <b-overlay :show="loginOverlay" rounded="sm">

          <div class="card h-100">

            <div class="card-body ">
              <h4 class="card-title mb-3 text-center">เข้าสู่ระบบ</h4>
              <form @submit.prevent="handleSubmit()">

                <b-alert v-model="showDismissibleAlert" :variant="loginResultAlert" dismissible> {{ resultText }}
                </b-alert>


                <div class="form-floating mb-3 text-left">
                  <input type="text" class="form-control" id="floatingInput" v-model="username"
                    placeholder="name@example.com">
                  <label for="floatingInput">PSU Passport</label>
                </div>
                <div class="form-floating mb-3">
                  <input type="password" class="form-control" id="floatingPassword" v-model="password"
                    placeholder="Password">
                  <label for="floatingPassword">Password</label>
                </div>



                <button type="submit" class="btn btn-primary b-center">Log in</button>

              </form>
            </div>
          </div>


        </b-overlay>
      </div>

    </div>

  </div>

</template>

<script>
import { ref } from 'vue'
import { useToast } from 'bootstrap-vue-3'

export default {
  name: "LoginView",
  data() {
    return {
      toast: useToast(),
      score: 0,
      username: "",
      password: "",
      resultText: "",
      artest: [],
      loginOverlay: ref(false),
      showDismissibleAlert: ref(false),
      loginResultAlert: "danger,"
    }
  },
  methods: {
    async handleSubmit() { //กดส่ง
      let data = {
        username: this.username,
        password: this.password
      }
      // console.log(data)
      this.loginOverlay = true
      this.showDismissibleAlert = false
      try {
        await this.$store.dispatch('login', data) // เข้า action login ใน @/store/auth
        this.$router.replace('/main') //ไป Main
      } catch (err) { //ถ้ามัน Login ไม่ได้ จะได้รับ Error
        this.loginResultAlert = "danger"
        this.toast.show(
          { title: 'เข้าสู่ระบบ', body: err.message },
          { pos: 'top-right', variant: this.loginResultAlert }
        )

        this.loginOverlay = false
      }

      this.loginOverlay = false


    }
  },
}
</script>

<style>
.page {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100vh;
}

.page-center {
  justify-content: center;
}
</style>