<template>
  <div id="chart_random">
    <apexchart
      class="border border-success"
      type="line"
      height="350"
      :options="chartOptions"
      :series="series"
    >
    </apexchart>
    <button @click="addrandom()" class="btn btn-primary">Random</button>
    <button @click="toggleautorun()" class="btn btn-success" v-if="!start">
      เริ่ม
    </button>
    <button @click="toggleautorun()" class="btn btn-danger" v-else>หยุด</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      low: {
        name: "low",
        data: [],
      },
      high: {
        name: "high",
        data: [],
      },
      day: [],
      chartOptions: {
        chart: {
          height: 350,
          type: "line",
          zoom: {
            enabled: true,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        title: {
          text: "Tempature By Day",
          align: "left",
        },
        grid: {
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5,
          },
        },
        xaxis: {
          categories: ["D 1"],
        },
      },
      series: [
        {
          name: "low",
          the_id: "for_low",
          data: [],
        },
        {
          name: "height",
          the_id: "for_height",

          data: [],
        },
      ],
      daycnt: 1,
      timers: null,
      start: true,
    };
  },
  methods: {
    addrandom() {
      /*
      let low = (Math.random() * 30).toFixed()
      this.low.data.push(low)
      let lowarr = this.low
      let high = (((Math.random() * 30) + parseInt(30)).toFixed())
      let higharr = this.high

      this.high.data.push(high)

      this.daycnt++
      this.day.push(this.daycnt)




      this.series = [lowarr, higharr]
      this.chartOptions.xaxis.categories = this.day

      console.log(lowarr)
      console.log(higharr)
      */
      /**/
      let low = (Math.random() * 30).toFixed();

      this.series[0].data.push(low);
      let high = (Math.random() * 30 + parseInt(30)).toFixed();

      this.series[1].data.push(high);
      this.daycnt++;
      this.chartOptions.xaxis.categories.push("D " + this.daycnt);

      if (this.chartOptions.xaxis.categories.length > 50) {
        this.chartOptions.xaxis.categories.splice(0, 1);
        this.series[0].data.splice(0, 1);
        this.series[1].data.splice(0, 1);
      }
      // console.log(this.chartOptions.xaxis.categories)
    },
    toggleautorun() {
      if (this.start) {
        // true
        clearInterval(this.timers);
      } else {
        //false
        this.timers = setInterval(() => {
          // console.log('randoms')
          this.addrandom();
        }, 2000);
      }
      this.start = !this.start;
    },
  },
  mounted() {
    this.timers = setInterval(() => {
      // console.log('randoms')
      this.addrandom();
    }, 2000);
  },
  beforeUnmount() {
    clearInterval(this.timers);
  },
};
</script>

<style></style>
