<template>
  <div>
    <div class="container">
      <div class="block" :class="{ animate: block }"></div>
      <button @click="animateBlock">Animate</button>
    </div>


    <div class="container">
      <transition name="para">
        <p v-if="paragraph">paragraph</p>
      </transition>
      <!-- <transition name="para">
        <p v-if="paragraph">paragraph</p>
      </transition> -->

      <button @click="toggleParagraph">Text</button>
    </div>

    <div class="container">
      <transition name="fade-button" mode="out-in">
        <button @click="showUser" v-if="!user">Show User</button>
        <button @click="hideUser" v-else>Hide User</button>
      </transition>
    </div>
  </div>


</template>

<script>
export default {
  data() {
    return {
      block: false,
      paragraph: false,
      user: false
    }
  },
  methods: {
    animateBlock() {
      this.block = !this.block
    },
    toggleParagraph() {
      this.paragraph = !this.paragraph
    },
    showUser() {
      this.user = true
    },
    hideUser() {
      this.user = false
    }
  }
}
</script>

<style>



.block {
  width: 8rem;
  height: 8rem;
  background-color: #290033;
  margin-bottom: 2rem;
  /* transition: transform 0.3s ease-out; */

}

.container {
  max-width: 40rem;
  margin: 2rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2rem;
  border: 2px solid #ccc;
  border-radius: 12px;
}

.animate {
  /* transform: translateX(-150px); */
  animation: slide-scale 0.3s ease-out forwards;
}

.para-enter-from {
  /* opacity: 0;
  transform: translateY(-30px); */
}

.para-enter-active {
  /* transition: all 0.3s ease-out; */
  animation: slide-scale 0.3s ease-out;
}

.para-enter-to {
  /* opacity: 1;
  transform: translateY(0); */
}

.para-leave-from {
  /* opacity: 1;
  transform: translateY(0px); */
}

.para-leave-active {
  /* transition: all 0.3s ease-out; */
  animation: slide-scale 0.3s ease-out;

}

.para-leave-to {
  /* opacity: 0;
  transform: translateY(-30px); */
}

.fade-button-enter-from {
  opacity: 0;
  transform: translateY(-30px);
}

.fade-button-leave-from {
  opacity: 1;
  transform: translateY(0px);

}

.fade-button-enter-active {
  transition: all 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940);
}

.fade-button-leave-active {
  transition: all 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940);
}

.fade-button-enter-to {
  opacity: 1;
  transform: translateY(0px);

}

.fade-button-leave-to {
  opacity: 0;
  transform: translateY(30px);

}

@keyframes slide-scale {
  0% {
    transform: translate(0) scale(1);
  }

  70% {
    transform: translate(-120px) scale(1.1);
  }

  100% {
    transform: translate(-150px) scale(1);
  }
}

flip-enter-active {
  animation: flip-in-hor-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

flip-leave-active {
  animation: flip-out-hor-bottom 0.45s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

@keyframes flip-in-hor-bottom {
  0% {
    transform: rotateX(80deg);
    opacity: 0;
  }

  100% {
    transform: rotateX(0);
    opacity: 1;
  }
}

@keyframes flip-out-hor-bottom {
  0% {
    transform: rotateX(0);
    opacity: 1;
  }

  100% {
    transform: rotateX(-70deg);
    opacity: 0;
  }
}

</style>