import { createRouter, createWebHistory } from 'vue-router'
// import HomeView from '../views/HomeView.vue'
import OverlayView from '../views/OverlayView.vue';
import LoginView from '../views/LoginView.vue';
import MainView from '../views/MainVIew.vue';
import store from '@/store';
import UserView from '@/views/UserView.vue';
import PageNotFound from '@/views/PagenotfoundView.vue';
import AnimateView from '@/views/AnimateView.vue';

// Set Routes
const routes = [{
    path: '/',
    redirect: '/main'
    // name: 'home',
    // component: HomeView
},
{
    path: '/overlay',
    name: 'overlay',
    component: OverlayView,
    meta: { reqAuth: false, needRole: false }
},
{
    path: '/login',
    name: 'login',
    component: LoginView,
    meta: { reqUnAuth: true, needRole: false }
}, {
    path: '/main',
    name: 'main',
    component: MainView,
    meta: { reqAuth: false, needRole: false }
}, {
    path: '/users',
    name: "user",
    component: UserView,
    meta: { reqAuth: false, needRole: true, reqRole: 1000 }
}, {
    path: '/animate',
    name: "animate",
    component: AnimateView,
    meta: { reqAuth: false }
}, {
    path: "/:pathMatch(.*)*",
    component: PageNotFound
}
]

// สร้าง Router
const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

router.beforeEach((to, _, next) => { //ก่อนไปหน้าที่ต้องการ (น่าจะรัน trylogin ก่อน)

    if (to.meta.reqAuth && store.getters['username'] == null) { // Login ไม่ได้ ไป Login ก่อน
        next('/login');
    } else if (to.meta.reqUnAuth && store.getters['username'] != null) { // Login แล้วอยู่หน้า Login
        next('/main');
    } else {
        // console.log(store.getters['role'])
        const role = localStorage.getItem('role') == null ? 0 : localStorage.getItem('role'); //getrole
        if (to.meta.needRole && to.meta.reqRole > role) { //ต้องการ Role และค่า Role ไม่ถึง ไปหน้า Main
            next('/main');
        } else { //ครบทุกองค์ประกอบ
            next();

        }
    }

})

export default router