<template>
  <div>
    <transition name="route" >
      <!-- <div class="d-none d-md-block"> -->
      <NavBar v-if="username"></NavBar>

      <!-- </div> -->
    </transition>
    <div class="container-fluid">

      <router-view v-slot="slotProps">
        <transition name="route" mode="out-in">
          <component :is="slotProps.Component"></component>
        </transition>
      </router-view>

    </div>

    <transition name="route">
      <div class="d-none d-md-none">
        <SwipeBottomNavigation :options="options" v-model="selected" v-if="username" />
      </div>
    </transition>
  </div>

</template>

<script>
import { mapGetters } from 'vuex';
import NavBar from './components/NavBar.vue';
import { SwipeBottomNavigation } from 'bottom-navigation-vue';
import "bottom-navigation-vue/dist/style.css";
// import axios from 'axios'

export default {

  components: { NavBar, SwipeBottomNavigation },
  computed: { // จะรันตอนที่ตัวแปรมีการเปลี่ยนแปลง
    ...mapGetters(['username'])
  },
  async created() { // รันหลังจากสร้าง
    await this.$store.dispatch('tryLogin'); // ลอง Login โดยตรวจสอบจาก Token
    //see @/store/auth
    // console.log(this.$route.name)
    this.selected = this.$route.name


  }, data: () => ({
    selected: 'main',
    options: [
      { id: 'main', icon: 'fa-solid fa-house', title: 'Home', path: { name: "main" } },
      { id: 'animate', icon: 'fa-solid fa-play', title: 'animate', path: { name: "animate" } },
      { id: 'user', icon: 'fa-solid fa-user', title: 'จัดการผู้ใช้', path: { name: "user" } },
    ]
  }),
}
</script>
