<template>
  <div id="chart-ex">
    <apexchart class="border border-success" type="bar" :options="options" :series="series">

    </apexchart>
    <input type="number" v-model="max" />
    <button @click="add(10)" class="btn btn-success">Add 10</button>
    <button @click="random()" class="btn btn-primary">Random</button>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      options: {
        chart: {
          stacked: true,

          stackType: '100%'
        }, responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        xaxis: {
          categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998, 1999]
        }
      },
      series: [{
        name: 'series-1',
        data: [30, 40, 45, 50, 49, 60, 70, 91, 10]
      }, {
        name: 'series-2',
        data: [80, 70, 55, 60, 19, 20, 30, 11, 20]
      }, {
        name: 'series-3',
        data: [10, 20, 30, 40, 50, 60, 70, 81, 30]
      }],
      max: 100,
      timer: null
    }
  }, async beforeCreate() {
    let th = await axios.get('https://cdn.jsdelivr.net/npm/apexcharts/dist/locales/th.json')
    console.log(th.data)
    this.options.chart['locales'] = th.data
    this.options.chart['defaultLocale'] = 'th'
    console.log(this.options)

  }, methods: {
    add(val) {
      // console.log(this.series[0].data.map((num) => num + val))
      // console.log(this.series)
      // this.series[0].data = this.series[0].data.map((num) => parseInt(num) + val)
      for (let idx = 0; idx < this.series.length; idx++) {
        const data = this.series[idx].data;
        this.series[idx].data = data.map((num) => parseInt(num) + val)

      }
    },
    random() {
      // this.series[0].data = this.series[0].data.map(() => (Math.random() * this.max).toFixed())

      for (let idx = 0; idx < this.series.length; idx++) {
        const data = this.series[idx].data;
        this.series[idx].data = data.map(() => (Math.random() * this.max).toFixed())

      }

    }
  }, mounted() {
    this.timer = setInterval(() => {
      // console.log('random')
      this.random()
    }, 2000);
  }, beforeUnmount() {
    clearInterval(this.timer)
  }

}
</script>

