<template>
  <b-card>
    <b-overlay :show="showCustomEx" rounded="sm" @shown="onShown" @hidden="onHidden" opacity="1">
      <b-card title="Card with custom overlay content" :aria-hidden="showCustomEx ? 'true' : null">
        <b-card-text>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</b-card-text>
        <b-card-text>Click the button to toggle the overlay:</b-card-text>
        <b-button :disabled="showCustomEx" variant="primary" @click="showCustomEx = true">
          Show overlay
        </b-button>
      </b-card>
      <template #overlay>
        <div class="text-center">
          <b-icon icon="stopwatch" font-scale="3" size="lg" animation="cylon"></b-icon>
          <p id="cancel-label">กำลังยืนยันตัวตน...</p>
          <b-button variant="outline-danger" size="sm" aria-describedby="cancel-label" @click="showCustomEx = false">
            ยกเลิก
          </b-button>
        </div>
      </template>
    </b-overlay>
  </b-card>
</template>

<script>
import { ref } from 'vue'
export default {
  data() {
    return {
      showCustomEx: ref(false)
    }
  }
}
</script>

